.hero-container {
  width: 100%;
  height: 100vh;
  position: relative; }

.hero-img {
  width: 100%;
  height: inherit;
  -o-object-fit: cover;
     object-fit: cover; }

.hero-info {
  text-align: center;
  position: absolute;
  top: 25%;
  padding: 30px;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%; }

.hero-button {
  border: 2px solid #6d946d;
  border-radius: 8px;
  cursor: pointer;
  width: 145px;
  height: 50px;
  padding: 8px;
  margin-top: 20px; }

@media (min-width: 800px) {
  .hero-button {
    height: 60px;
    width: 180px; } }



